<template>
    <div>
         <div class="container">
            <div class="handle-box">
                <el-input v-model="query.userName" placeholder="用户名" class="handle-input mr10"></el-input>
                <el-select v-model.number="query.userType" placeholder="请选择">
                    <el-option v-for="item in UserTypeAll" :label="item.name" :key="item.value" :value="item.value"></el-option>
                </el-select>
                <el-button type="primary" icon="el-icon-search" @click="handleSearch">搜索</el-button>
            </div>
            <el-table
                    :data="tableData"
                    border
                    class="table"
                    ref="multipleTable"
                    header-cell-class-name="table-header"
                    :summary-method="getSummaries"
                    show-summary
            >
                <!--                <el-table-column type="selection" width="55" align="center"></el-table-column>-->
                <el-table-column prop="Id" label="ID" width="55" align="center"></el-table-column>
                <el-table-column prop="UserName" label="用户名" width="100"></el-table-column>
                <el-table-column prop="FullName" label="姓名"></el-table-column>
                <el-table-column label="余额">
                    <template slot-scope="scope">{{scope.row.Gold|money}}</template>
                </el-table-column>
                <el-table-column label="积分">
                    <template slot-scope="scope">{{scope.row.Point}}</template>
                </el-table-column>
<!--                <el-table-column label="剩余佣金">-->
<!--                    <template slot-scope="scope">{{scope.row.MemberFilmRemainBrokerage|money}}</template>-->
<!--                </el-table-column>-->

                <el-table-column label="投资数">
                    <template slot-scope="scope">{{scope.row.OrderCount}}</template>
                </el-table-column>

                <el-table-column label="投资金额">
                    <template slot-scope="scope">{{scope.row.BuyGold|money}}</template>
                </el-table-column>

                <el-table-column label="到账分红">
                    <template slot-scope="scope">{{scope.row.ArriveRebateGold|money}}</template>
                </el-table-column>
                <el-table-column label="预估分红">
                    <template slot-scope="scope">{{scope.row.EstimateRebateGold|money}}</template>
                </el-table-column>

                <el-table-column label="会员级别">
                    <template slot-scope="scope">{{scope.row.MemberLevel|MemberLevelName}}</template>
                </el-table-column>
                <el-table-column label="团队级别">
                    <template slot-scope="scope">{{scope.row.TeamLevel|TeamLevelName}}</template>
                </el-table-column>
                <el-table-column label="用户类别">
                    <template slot-scope="scope">{{scope.row.UserType|UserTypeName}}</template>
                </el-table-column>

<!--                <el-table-column label="操作" width="140" align="center">-->
<!--                    <template slot-scope="scope">-->
<!--                        <el-button-->
<!--                                type="text"-->
<!--                                icon="el-icon-edit"-->
<!--                                @click="handleEdit(scope.$index, scope.row)"-->
<!--                        >查看</el-button>-->


<!--                    </template>-->
<!--                </el-table-column>-->
            </el-table>
            <div class="pagination">
                <el-pagination
                        background
                        layout="total, prev, pager, next"
                        :current-page="query.pageIndex"
                        :page-size="query.pageSize"
                        :total="pageTotal"
                        @current-change="handlePageChange"
                ></el-pagination>
            </div>
        </div>

        <!-- 编辑弹出框 -->
        <el-dialog title="查看" :visible.sync="editVisible" width="90%">
            <el-form ref="form" :model="form" label-width="100px">
                <el-form-item label="用户名" width="100">
                    <el-input v-model="form.UserName" readonly></el-input>
                </el-form-item>

                <el-form-item label="昵称">
                    <el-input v-model="form.Nickname"></el-input>
                </el-form-item>

            </el-form>

            <span slot="footer" class="dialog-footer">
                <el-button @click="editVisible = false">取 消</el-button>
            </span>
        </el-dialog>

    </div>
</template>
<script>
    import VueCropper  from 'vue-cropperjs';
    import { request } from '../../utils/http';

    export default {
        name: 'UserReportList',
        data() {
            return {
                defaultAvatarSrc: require('../../assets/img/default_avatar.jpg'),
                dialogVisible: false,
                saveMoneyVisible:false,
                UserTypeAll:[
                    {name:'全部',value:0},
                    {name:'一般玩家',value:1},
                    {name:'业务玩家',value:2},
                ],
                query: {
                    userType: 0,
                    state: 0,
                    userName: '',
                    pageIndex: 1,
                    pageSize: 10
                },
                tableData: [],
                GroupData:{},
                multipleSelection: [],
                delList: [],
                editVisible: false,
                pageTotal: 0,
                form: {},
                formSaveMoney:{UserId:0,UserName:'',Money:100},
                idx: -1,
                id: -1
            };
        },
        created() {
            this.getData();
        },
        mounted() {
            // this.editor = this.$refs.myQuillEditor.quill;

        },
        beforeDestroy(){
            // this.editor = null;
            // delete this.editor;
        },
        methods: {
            getSummaries(){
                const sums = [];
                sums[1] = "合计："+ this.GroupData.C+"条";
                sums[3] = this.GroupData.Gold.toFixed(2);
                sums[4] = this.GroupData.Point;
                // sums[5] = this.GroupData.MemberFilmRemainBrokerage.toFixed(2);

                sums[5] = this.GroupData.OrderCount;
                sums[7] = this.GroupData.BuyGold.toFixed(2);
                sums[8] = this.GroupData.ArriveRebateGold.toFixed(2);
                sums[9] = this.GroupData.EstimateRebateGold.toFixed(2);

                return sums;
            },
            getData() {
                request({ url: 'filmbll/getgameuserreportlist', method: 'post', data: this.query }).then((res) => {
                    if (res.code == 200) {
                        this.tableData = res.obj.ListData;
                        this.GroupData = res.obj.GroupData;
                        this.pageTotal = res.obj.PageTotal;
                    }
                });
            },
            // 触发搜索按钮
            handleSearch() {
                this.$set(this.query, 'pageIndex', 1);
                this.getData();
            },
            // 分页导航
            handlePageChange(val) {
                this.$set(this.query, 'pageIndex', val);
                this.getData();
            }
        }
    };
</script>

<style scoped>
    .handle-box {
        margin-bottom: 20px;
    }

    .handle-select {
        width: 120px;
    }

    .handle-input {
        width: 300px;
        display: inline-block;
    }
    .table {
        width: 100%;
        font-size: 14px;
    }
    .red {
        color: #ff0000;
    }
    .mr10 {
        margin-right: 10px;
    }
    .table-td-thumb {
        display: block;
        margin: auto;
        width: 40px;
        height: 40px;
    }
    .ivu-upload {
        display: none;
    }
    .ivu-btn{

    }
</style>
